.news {
  &__top {
    background-image: url("/images/news/news.png");
  }
  &__title {
    font-size: 32px;
    line-height: 54px;
    padding-bottom: 60px;
    text-align: center;
  }
  &__item {
    display: flex;
    line-height: 24px;
  }
  &__date {
    padding-right: 51px;
  }
  &__link {
    text-decoration: underline;
  }
}
